global.Parser = {};
import trees from './trees'
export default {
	components: {
		trees
	},
	name: 'trees',
	data() {
		return {
			controls: {},

			imgLoad: false,


			loadVideo: true

		}
	},
	props: {
		nodes: Array,

		lazyLoad: Boolean,




	},
	mounted() {
		// 获取顶层组件
		this.top = this.$parent;
		while (this.top.$options.name != 'parser') {
			if (this.top.top) {
				this.top = this.top.top;
				break;
			}
			this.top = this.top.$parent;
		}
	},

	beforeDestroy() {
		if (this.observer)
			this.observer.disconnect();
	},

	methods: {

		play(e) {
			if (this.top.videoContexts.length > 1 && this.top.autopause)
				for (var i = this.top.videoContexts.length; i--;)
					if (this.top.videoContexts[i].id != e.currentTarget.dataset.id)
						this.top.videoContexts[i].pause();
		},

		imgtap(e) {
			var attrs = e.currentTarget.dataset.attrs;
			if (!attrs.ignore) {
				var preview = true, data = {
					id: e.target.id,
					src: attrs.src,
					ignore: () => preview = false
				};
				global.Parser.onImgtap && global.Parser.onImgtap(data);
				this.top.$emit('imgtap', data);
				if (preview) {
					var urls = this.top.imgList,
						current = urls[attrs.i] ? parseInt(attrs.i) : (urls = [attrs.src], 0);
					uni.previewImage({
						current,
						urls
					})
				}
			}
		},
		imglongtap(e) {
			var attrs = e.item.dataset.attrs;
			if (!attrs.ignore)
				this.top.$emit('imglongtap', {
					id: e.target.id,
					src: attrs.src
				})
		},
		linkpress(e) {
			var jump = true,
				attrs = e.currentTarget.dataset.attrs;
			attrs.ignore = () => jump = false;
			global.Parser.onLinkpress && global.Parser.onLinkpress(attrs);
			this.top.$emit('linkpress', attrs);
			if (jump) {

				if (attrs['app-id']) {
					return uni.navigateToMiniProgram({
						appId: attrs['app-id'],
						path: attrs.path
					})
				}

				if (attrs.href) {
					if (attrs.href[0] == '#') {
						if (this.top.useAnchor)
							this.top.navigateTo({
								id: attrs.href.substring(1)
							})
					} else if (attrs.href.indexOf('http') == 0 || attrs.href.indexOf('//') == 0) {




						uni.setClipboardData({
							data: attrs.href,
							success: () =>
								uni.showToast({
									title: '链接已复制'
								})
						})

					} else
						uni.navigateTo({
							url: attrs.href
						})
				}
			}
		},
		error(e) {
			var context, target = e.currentTarget,
				source = target.dataset.from;
			if (source == 'video' || source == 'audio') {
				// 加载其他 source
				var index = this.controls[target.id] ? this.controls[target.id].index + 1 : 1;
				if (index < target.dataset.source.length)
					this.$set(this.controls, target.id + '.index', index);
				if (source == 'video') context = uni.createVideoContext(target.id, this);
			}
			this.top && this.top.$emit('error', {
				source,
				target,
				errMsg: e.detail.errMsg,
				errCode: e.detail.errCode,
				context
			});
		},
		_loadVideo(e) {
			this.$set(this.controls, e.currentTarget.id, {
				play: true,
				index: 0
			})
		}
	}
}
